import React, { useState, useEffect } from 'react'

import Link from 'next/link'

import NavBar from '../components/NavBar'


const BookPage = () => {
    const [test, setTest] = useState([])
    const loremFiller = "yesyseyseyseyseyse"

    useEffect(() => {
        fetchBooks()
    }, [])

    const fetchBooks = async () => {
        let res = await fetch('https://www.googleapis.com/books/v1/volumes?q=python')
        let data = await res.json()

        setTest(data.items)
        console.log(data.items)
    }

    const renderBooks = () => {
        if(test.length) {
            return test.map(book => {
                return (
                    <div className="row" key={book.id}>
                        <div className="col-sm-3">
                            <img src={book.volumeInfo.imageLinks.thumbnail}/>
                        </div>
                        <div className="col-sm-9">
                            <h4>{book.volumeInfo.title}</h4>
                            <p>{book.volumeInfo.description}</p>
                        </div>
                    </div>
                )
            })
        }
        return null
    }

    return (
        <div className="container">
            <h3 className="text-center">Some Books!</h3>
            <div class="jumbotron">
                <div class="over container body-content">
                    <h1 class="display-5">A curated directory of books for shit kunts to help you along your python programming peregrination</h1>
                    <p class="lead">Buy them all mate ;)</p>
                </div>
            </div>
            {renderBooks()}
            <Link href="/index"><button className="justify-content-end btn btn-warning">Back</button></Link>
        </div>
    )
}

export default BookPage